import React from 'react';

class Info extends React.Component {
  constructor(props) {
    super();
    this.text = {}
    this.text['h_type'] = new Array(5);
    this.text['s_type'] = new Array(5);
    this.text['p_type'] = new Array(5);

    this.text['h_type'][0] = '';
    this.text['h_type'][1] = '';
    this.text['h_type'][2] = 'F1F2';
    this.text['h_type'][3] = 'HoldBPF1LPFF1F2';
    this.text['h_type'][4] = 'Vowel Delay';

    this.text['s_type'][0] = 'Attack+Hold';
    this.text['s_type'][1] = '';
    this.text['s_type'][2] = '';
    this.text['s_type'][3] = 'Vowel Delay';
    this.text['s_type'][4] = '';

    this.text['p_type'][0] = '';
    this.text['p_type'][1] = ''
    this.text['p_type'][2] = '';
    this.text['p_type'][3] = 'Attack,Hold,Release';
    this.text['p_type'][4] = 'Vowel Delay';
  }

  getType(c)
  {
    switch (c) {
      case 'h':
        return 'h_type';
      case 's':
      case 'sy':
        return 's_type';
      case 'p':
      case 'k':
      case 't':
      case 'c':
      case 'ts':
        return 'p_type';
      default:
        break;
    }
  }

  render() {
    return (
      <div className="info">
        <div>[{this.props.value}]</div>
        <div>{this.text[this.getType(this.props.value)][0]}</div>
        <div>{this.text[this.getType(this.props.value)][1]}</div>
        <div>{this.text[this.getType(this.props.value)][2]}</div>
        <div>{this.text[this.getType(this.props.value)][3]}</div>
        <div>{this.text[this.getType(this.props.value)][4]}</div>
      </div>
    );
  }
}

export default Info;




